import { Component, Input, ViewEncapsulation, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material/icon';
import { MatMenuTrigger } from '@angular/material/menu';

export type SidekickColor = 'red' | 'green' | 'gray';
/**
 *
 */
@Component({
  selector: 'crem-metric',
  template: `<mat-card class="costar-hero-metric-card">
	<mat-card-header>
		<mat-card-title>{{title}}</mat-card-title>
		<div class="addl-info-wrapper">
      <button mat-icon-button class="infobutton" color="#0559B3" [matMenuTriggerFor]="tooltipMenu">
        <mat-icon>info</mat-icon>
      </button>
			<mat-menu #tooltipMenu="matMenu" [overlapTrigger]="true">
				<button mat-menu-item class="hero-tooltip">
					{{tooltip}}
        </button>
			</mat-menu>
		</div>
	</mat-card-header>
	<mat-card-content>
		<div class="hero-value">
			{{heroMetric}}
		</div>
		<div class="sidekick-value-wrapper" *ngIf="sidekickMetric">
			<div class="sidekick-value" [ngClass]="sidekickColor" >{{sidekickMetric}}</div>
			<!-- <div class="sidekick-value" *ngIf="sidekickMetricB">{{sidekickMetricB}}</div> -->
		</div>
	</mat-card-content>
	<mat-card-footer>
		<mat-card-subtitle>
			{{subtitle}}
		</mat-card-subtitle>
	</mat-card-footer>
</mat-card>`,
styleUrls: ['hero-metric.component.scss']
})
export class HeroMetricComponent {
  @Input() title: string;
  @Input() subtitle: string;
  @Input() heroMetric: string;
  @Input() sidekickMetric: string;
  @Input() sidekickColor: SidekickColor;
  @Input() tooltip: string;

  constructor(iconRegistry: MatIconRegistry, sanitizer: DomSanitizer) {
    iconRegistry.addSvgIcon('info',
      sanitizer.bypassSecurityTrustResourceUrl('assets/images/info.svg'));
}

}
